<template>
  <div class="scroll-bar"
    :id="scrollBarId"
    style="height: 100%">
    <slot></slot>
  </div>
</template>

<script>
import OverlayScrollbars from 'overlayscrollbars'
import 'overlayscrollbars/css/OverlayScrollbars.min.css'
export default {
  name: 'ScrollBar',
  props: {
    //Dom Id,
    id: {
      type: String
    },
    //os-theme-darkos-theme-light
    theme: {
      type: String,
      default: 'os-theme-dark'
    },
    //
    onInitialized: {
      type: Function
    },
    //
    onDestroyed: {
      type: Function
    },
    //scroll
    onScrollStart: {
      type: Function
    },
    //scroll
    onScroll: {
      type: Function
    },
    //scroll
    onScrollStop: {
      type: Function
    },
    //scroll
    onOverflowChanged: {
      type: Function
    },
    //scroll
    onOverflowAmountChanged: {
      type: Function
    },
    //scroll
    onDirectionChanged: {
      type: Function
    },
    //scroll
    onContentSizeChanged: {
      type: Function
    }
  },
  data() {
    return {
      scrollBar: null,
      scrollBarId: this.id ? this.id : 'scroll-bar-' + Date.now() + Math.random()
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initScrollBar()
    })
  },
  methods: {
    initScrollBar() {
      this.scrollBar = OverlayScrollbars(document.getElementById(this.scrollBarId), {
        className: this.theme,
        scrollbars: {
          autoHide: 'leave'
        },
        callbacks: {
          onInitialized: this.onInitialized,
          onDestroyed: this.onDestroyed,
          onScrollStart: this.onScrollStart,
          onScroll: this.onScroll,
          onScrollStop: this.onScrollStop,
          onOverflowChanged: this.onOverflowChanged,
          onOverflowAmountChanged: this.onOverflowAmountChanged,
          onDirectionChanged: this.onDirectionChanged,
          onContentSizeChanged: this.onContentSizeChanged
        }
      })
    },
    update() {
      this.scrollBar.update()
    }
  }
}

</script>
<style lang='stylus' scoped>
</style>


